@import url('https://fonts.googleapis.com/css?family=Poppins');
@import url('https://fonts.googleapis.com/css?family=Nunito');

.game-wrapper {
    height: 100vh;
    padding: 0;
    box-sizing: border-box;
    overflow: auto;
    background: url('../assets/SpinWheelPictures/SpinWheelBackground.png') no-repeat;
    background-size: cover;
    background-position: center;
    overflow-x: hidden;
}

.game-container {
    height: fit-content;
}

button {
    -webkit-tap-highlight-color: transparent;
}

.text-turn {
    text-align: center;
    width: 100%;
    font-family: 'Nunito';
    font-size: 13px;
    line-height: 17.73px;
}

.title-box {
    width: 338px;
    text-align: center;
    margin: auto;
    margin-top: 40px;
}

.text-gradient {
    text-transform: uppercase;
    background: linear-gradient(69.12deg, #A90069 1.98%, #EC008C 75.87%);
    background-clip: text;
    color: transparent;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-text-fill-color: transparent;
}

.text-title {
    text-transform: uppercase;
    font-size: 30px;
    line-height: 45px;
    font-family: 'Poppins';
}

.round-box {
    margin: auto;
    width: 341px;
    height: 341px;
    position: relative;
}

.img-round {
    padding: 0;
    height: 100%;
    width: 100%;
    z-index: -1;
}

.list-item {
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    font-size: 9px;
    font-family: 'Impact Regular';
    color: #ffffff;
    width: fit-content;
}

    .list-item:nth-child(2) {
        transform: rotate(252deg) translate(61px) rotate(18deg);
        transform-origin: top left;
    }

    .list-item:nth-child(3) {
        transform: rotate(302deg) translate(103px) rotate(14deg);
    }

    .list-item:nth-child(4) {
        transform: rotate(348deg) translate(85px) rotate(12deg);
    }

    .list-item:nth-child(5) {
        transform: rotate(422deg) translate(61px) rotate(342deg);
    }

    .list-item:nth-child(6) {
        transform: rotate(75deg) translate(57px) rotate(374deg);
        color: #000000;
        transform-origin: top left;
    }

    .list-item:nth-child(7) {
        transform: rotate(153deg) translate(107px) rotate(341deg);
    }

    .list-item:nth-child(8) {
        transform: rotate(185deg) translate(144px) rotate(355deg);
    }

    .list-item:nth-child(9) {
        transform: rotate(224deg) translate(125px) rotate(362deg);
    }

.spinicon {
    position: absolute;
    top: 33%;
    left: 33%;
}

    .spinicon img {
        width: 115px;
    }

.turn-spin-box {
    background: #ffffff;
    display: flex;
    align-items: center;
    margin: auto;
    margin-top: 15px;
    width: 170px;
    height: fit-content;
    border-radius: 30px;
}

.button-spin-box {
    text-align: center;
    margin: auto;
    margin-top: 25px;
    border-radius: 15px;
    height: fit-content;
    position: relative;
    z-index: 0;
}

.button-spin {
    border-radius: 15px;
    width: 173px;
    height: 35px;
    text-align: center;
    color: #ffffff;
    border: none;
    font-weight: 700;
    font-family: 'Poppins';
    font-size: 12px;
    line-height: 18px;
    background: linear-gradient(69.12deg, #FC6F8B 1.98%, #FC6F8B 75.87%);
    cursor: pointer;
}

.description-box {
    margin: 15px 21px 10px 21px;
    height: fit-content;
    overflow: auto;
}

.text-description {
    font-family: 'Poppins';
    font-size: 14px;
    line-height: 18px;
    color: #626262;
    margin-bottom: 2px;
}

.content-description {
    text-align: justify;
    font-family: 'Poppins';
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
    color: #4B4B4B;
}

.bottom-box {
    display: flex;
    justify-content: space-evenly;
    width: auto;
    height: 100px;
    background: #FFFFFF;
    border: 0.5px solid #FFFFFF;
    border-radius: 30px 30px 0px 0px;
    position: relative;
}

.button-footer {
    background: linear-gradient(69.12deg, #A90069 1.98%, #EC008C 75.87%);
    box-shadow: 0px 12px 19px -9px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    width: 130px;
    height: 33px;
    font-family: 'Poppins';
    font-size: 10px;
    font-weight: 700 bold;
    line-height: 15px;
    color: #ffffff;
    border: none;
    margin-top: 25px;
    position: absolute;
    bottom: 40px;
}

.modal {
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0.4);
    bottom: 0;
}

.modal-content {
    position: fixed;
    bottom: 0;
    background-color: #fefefe;
    border: 1px solid #888;
    width: 100%;
    height: 291px;
    border-radius: 30px 30px 0px 0px;
}

.modal-box {
    margin: 20px 25px 30px 26px;
    height: 100%;
}

.modal-header {
    font-family: 'Poppins';
    font-size: 14px;
    color: #626262;
    height: 15%;
}

    .modal-header h2 {
        font-weight: 700 bold;
    }

.modal-body {
    font-family: 'Poppins';
    font-size: 12px;
    color: #4B4B4B;
    height: 60%;
    overflow: auto;
    padding-bottom: 10px;
}

    .modal-body p {
        font-weight: 400;
    }

.modal-footer {
    width: 100%;
    text-align: center;
}

.close {
    margin-top: -2px;
    height: 34px;
    width: 325px;
    border-radius: 15px;
    background: linear-gradient(69.12deg, #A90069 1.98%, #E7008A 1.98%, #EC008C 75.87%);
    border: none;
    color: #ffffff;
    font-family: 'Poppins';
    font-size: 12px;
    line-height: 18px;
}

.popUpPrize {
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0.4);
}

.popup-content {
    background-color: #fefefe;
    margin: 160px auto;
    width: 317px;
    height: 385px;
    border-radius: 30px;
}

.popup-header {
    text-align: center;
    width: 100%;
}

.text-cg {
    font-size: 28px;
    padding-top: 23px;
    line-height: 42px;
    margin-bottom: 0;
}

.popup-body p {
    font-weight: 700;
    font-size: 15px;
    line-height: 22.5px;
}

.popup-body {
    margin-top: 16px;
    text-align: center;
}

.popup-img {
    width: 100%;
}

.popup-footer {
    display: flex;
    justify-content: space-evenly;
    margin-top: 0;
}

.button-popup-footer {
    height: 35px;
    width: 124px;
    border-radius: 15px;
}

.continue {
    color: #ffffff;
    background: linear-gradient(69.12deg, #A90069 1.98%, #E7008A 1.98%, #EC008C 75.87%);
    box-shadow: 0px 12px 19px -9px rgba(0, 0, 0, 0.15);
    font-family: 'Poppins';
    font-size: 14px;
    font-weight: 700;
    border: none;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
}

.invalidModal {
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0.4);
    bottom: 0;
}

.invalidModal-content {
    background-color: #fefefe;
    margin: 160px auto;
    width: 317px;
    height: 385px;
    position: relative;
}

    .invalidModal-content p {
        position: absolute;
        top: 5%;
        left: 2%;
    }

.invalid-img {
    position: absolute;
    top: 20%;
    width: 100%;
    height: 100%;
}

.outOfChancesModal {
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0.4);
}

.outOfChances-content {
    background-color: #fefefe;
    margin: 160px auto;
    width: 317px;
    height: 385px;
    border-radius: 30px;
}

.outOfChances-header {
    text-align: center;
    width: 100%;
}

.text-chance {
    font-size: 12px;
    padding-top: 23px;
    background: linear-gradient(69.12deg, #A90069 1.98%, #EC008C 75.87%);
    background-clip: text;
    color: transparent;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-text-fill-color: transparent;
}

.outOfChances-body p {
    font-weight: 700;
    font-size: 15px;
    line-height: 22.5px;
}

.outOfChances-body {
    margin-top: 16px;
    text-align: center;
}

.outOfChances-img {
    width: 80%;
}

.outOfChances-footer {
    display: flex;
    justify-content: space-evenly;
    margin-top: 32px;
}

.closeChance {
    height: 35px;
    width: 124px;
    border-radius: 15px;
    color: #ffffff;
    background: linear-gradient(69.12deg, #A90069 1.98%, #E7008A 1.98%, #EC008C 75.87%);
    box-shadow: 0px 12px 19px -9px rgba(0, 0, 0, 0.15);
    font-family: 'Poppins';
    font-size: 14px;
    font-weight: 700;
    border: none;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
}

@media screen and (min-height: 700px) {
    .description-box {
        margin-top: 35px;
        margin-bottom: 50px;
    }

    .bottom-box {
        min-height: 120px;
    }

    .button-footer {
        bottom: 50px;
    }
}

@media screen and (min-height: 840px) {
    .bottom-box {
        min-height: 150px;
    }
}

@media screen and (min-height: 890px) {
    .bottom-box {
        min-height: 180px;
    }

    .description-box {
        margin-bottom: 70px;
    }
}
